.box {
    background-color: #fff;
    color: #838383;
    width: 300px;
    padding: 20px;
    border-radius: 8px;
    h1 {
        text-align: center;
        padding: 0;
        margin: 0;
        margin-bottom: 20px;
    }
    label {
        font-size: 13px;
    }
    input[type=number] {
        width: 50px;
        display: block;
    }
    .startButtonHolder {
        padding: 10px;
        text-align: center;
        .startButton {
            width: 70%;
        }
    }
}