.box {
    max-width: 300px;
    margin-top: 10px;
    .heart {
        display: inline-block;
        margin: 0 3px;
        & > div {
            display: flex;
            justify-content: center;
            & svg {
                width: 40px;
                height: 40px;
            }
            & path {
                fill: #ff0000;
                stroke: #fff;
            }
            p {
                position: absolute;
                font-size: 12px;
                font-weight: bold;
            }
        }
        &.addition {
            & path {
                fill: #0000ff;
            }
        }
        &.subtract {
            opacity: .2;
        }
    }
}