.content {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    overflow-y: auto;

    .score {
        text-align: center;
        font-size: 13px;
        .bigScore {
            display: inline-block;
            width: 80px;
            height: 80px;
            border-radius: 40px;
            background-color: #0000ff;
            color: #fff;
            text-align: center;
            line-height: 80px;
            font-size: 40px;
            margin: 0;
        }
        .right {
            padding: 3px;
            border-radius: 20px;
            font-size: 15px;
            background-color: lightgreen;
            color: black;
        }
        .wrong {
            padding: 3px;
            border-radius: 20px;
            background-color: red;
            font-size: 15px;
            color: white;
        }
    }

    .results {
        margin: 20px auto;
        max-width: 80%;
        display: flex;
        flex-wrap: wrap;
        .result {
            background-color: white;
            color: black;
            border: 1px solid #000;
            .vertical {
                margin: 0 10px;
                width: 87px;
                text-align: right;
                border-bottom: 1px solid #000;
            }
            .regular {
                width: 100px;
            }
            .status {
                margin: 0;
                padding: 3px 0;
                display: inline-block;
                width: 100%;
                font-size: 13px;
                &.right {
                    background-color: lightgreen;
                }
    
                &.wrong {
                    background-color: red;
                    color: white;
                }
            }
        }
    }
}