.box {
  max-width: 300px;
  margin-top: 10px;
}
.box .heart {
  display: inline-block;
  margin: 0 3px;
}
.box .heart > div {
  display: flex;
  justify-content: center;
}
.box .heart > div svg {
  width: 40px;
  height: 40px;
}
.box .heart > div path {
  fill: #ff0000;
  stroke: #fff;
}
.box .heart > div p {
  position: absolute;
  font-size: 12px;
  font-weight: bold;
}
.box .heart.addition path {
  fill: #0000ff;
}
.box .heart.subtract {
  opacity: 0.2;
}

