.box {
  background-color: #fff;
  color: #838383;
  width: 300px;
  padding: 20px;
  border-radius: 8px;
}
.box h1 {
  text-align: center;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}
.box label {
  font-size: 13px;
}
.box input[type=number] {
  width: 50px;
  display: block;
}
.box .startButtonHolder {
  padding: 10px;
  text-align: center;
}
.box .startButtonHolder .startButton {
  width: 70%;
}

