.equation {
    font-size: 30px;
    text-align: center;
    .blink {
        width: 6px;
        height: 30px;
        background-color: #fff;
        display: inline-block;
        margin: 0 8px;
        position: relative;
        top: 5px;
        -webkit-animation: 1s linear infinite condemned_blink_effect; /* for Safari 4.0 - 8.0 */
        animation: 1s linear infinite condemned_blink_effect;
    }
    .vertical {
        width: 100px;
        position: relative;
        text-align: right;
        border-bottom: 2px solid #fff;
        p {
            margin: 10px 0;
            padding: 0;
        }
        .sign {
            margin-right: 8px;
        }
    }
    .guess {
        margin: 0 -8px 0 8px;
    }
}
header {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    color: #fff;
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: right;
    button {
        all: unset;
        padding: 0 15px;
        border: 1px solid #fff;
        font-size: 12px;
        flex: 1;
        white-space: nowrap;
        text-align: center;
        span {
            font-size: 16px;
            margin-right: 4px;
        }
        &:active {
            background-color: #838383;
        }
    }
}
@keyframes condemned_blink_effect {
    0% {
      visibility: hidden;
    }
    50% {
      visibility: hidden;
    }
    100% {
      visibility: visible;
    }
  }