.content {
  position: fixed;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  overflow-y: auto;
}
.content .score {
  text-align: center;
  font-size: 13px;
}
.content .score .bigScore {
  display: inline-block;
  width: 80px;
  height: 80px;
  border-radius: 40px;
  background-color: #0000ff;
  color: #fff;
  text-align: center;
  line-height: 80px;
  font-size: 40px;
  margin: 0;
}
.content .score .right {
  padding: 3px;
  border-radius: 20px;
  font-size: 15px;
  background-color: lightgreen;
  color: black;
}
.content .score .wrong {
  padding: 3px;
  border-radius: 20px;
  background-color: red;
  font-size: 15px;
  color: white;
}
.content .results {
  margin: 20px auto;
  max-width: 80%;
  display: flex;
  flex-wrap: wrap;
}
.content .results .result {
  background-color: white;
  color: black;
  border: 1px solid #000;
}
.content .results .result .vertical {
  margin: 0 10px;
  width: 87px;
  text-align: right;
  border-bottom: 1px solid #000;
}
.content .results .result .regular {
  width: 100px;
}
.content .results .result .status {
  margin: 0;
  padding: 3px 0;
  display: inline-block;
  width: 100%;
  font-size: 13px;
}
.content .results .result .status.right {
  background-color: lightgreen;
}
.content .results .result .status.wrong {
  background-color: red;
  color: white;
}

